import SwiperArea from "./SwiperArea";
import TextArea from "./TextArea";

import { ContentBox } from "../styles/SubStyle";

function ContentWrap(p){
  let data=p.propsValue;
  return(
  <ContentBox>
    <SwiperArea propsValue={data}/>
    <TextArea propsValue={data}/>
  </ContentBox>
  )
};

export default ContentWrap;