import { useEffect, useState } from "react";

import gsap from "gsap";
import ScrollToPlugin from "gsap/ScrollToPlugin";
import Nav from "./Nav.js";

import { isMobile } from "react-device-detect";
import { HeaderBox, Inner, Search, Logo, Tab } from '../styles/HeaderStyle.js';

function Header(){
	useEffect(() => {
		gsap.registerPlugin(ScrollToPlugin);

		let video=document.getElementById("main-video");

		video.addEventListener("loadeddata", function(){
			video.play();  
		});

		video.addEventListener("ended", function(){
			video.play();    
		});

		let pageList=document.querySelectorAll(".container > div");

		let n=0;
		let total=pageList.length; 
		let moving=false; 

		let winh;
		let winw;
		let Sub=document.querySelectorAll(".subpage");

		if(isMobile) {
			document.body.classList.add("isMobile");
			for (let i = 0; i < pageList.length; i++) {
				pageList[i].style.position = "relative";
			}
		}
		else {
			function categoryFn(){
				moving=true;
				winh=window.innerHeight;
	
				for(let i=0; i<total; i++){
					if(i <= n){
						if(i === n){
							gsap.to(pageList[i], {top:0, duration:0.5, onComplete:function(){
								moving=false;
								for(let j=0; j<total; j++){
									if(i === j){
										pageList[j].classList.add("active");
									}
									else {
										pageList[j].classList.remove("active");
									}
								}
							}});
						}
						else{
							gsap.to(pageList[i], {top:0, duration:0.5});
						}
					}
					else{
						gsap.to(pageList[i], {top:winh, duration:0.5});
					}
				}
			}
	
			categoryFn();
	
				document.addEventListener("wheel", function(e){
					if(moving) return;
				
					if(e.wheelDeltaY > 0){ 
						if(n > 0){
							n-=1;
						}
						else return;
					}
					else { 
						if(n < total-1){
							n+=1;
						}
						else return;
					}  
				
					categoryFn();
				});
	
			window.addEventListener("resize", function(){
				winw=window.innerWidth;
	
				function addStyle(i){
					let SubInner=Sub[i].firstElementChild.firstElementChild;
					SubInner.style.flexDirection = "row-reverse";
					SubInner.style.marginLeft = "auto";
					SubInner.style.marginRight = "3em";
				}
			
				function removeStyle(i) {
					let SubInner = Sub[i].firstElementChild.firstElementChild;
					SubInner.style.flexDirection = "";
					SubInner.style.marginRight = "";
				}
	
				if(winw > 1280){
					for(let i=0; i<Sub.length; i++){
						if (i % 2 === 1){
						addStyle(i);
						}
					}
				}
				else {
					for(let i=0; i<Sub.length; i++){
						if (i % 2 === 1){
						removeStyle(i);
						}
					}
				}
			});
		}

	}, []);


	const [isOpen, setIsOpen]=useState(false);

	const handleClick = () => {
		setIsOpen(!isOpen);
		gsap.to(".header", { height: isOpen ? "auto" : "300px", duration: 0.3 });
	};

	return(
		<>
			<HeaderBox className="header">
				<Inner>
					<Tab id="tab" className={isOpen ? "close" : ""} onClick={handleClick}>
						<span></span>
					</Tab>
					<Logo />
					<Nav />
					<Search />
				</Inner>
			</HeaderBox>
		</>
	);
};

export default Header;