import FooterColumn from "./FooterColumn";
import FooterAsset from "./FooterAsset";

import { FtContent } from "../styles/FooterStyle";

function FooterWrap(p){
  let data=p.propsValue;

  return(
    <FtContent>
      {
        data.map((d, i) => <FooterColumn key={i+1} propsValue={d}/>)
      }
      <FooterAsset />
  </FtContent>
  )
};

export default FooterWrap;