import { useSelector } from "react-redux";

import Header from "./Header";
import Main from "./Main";
import Sub from "./Sub";
import Footer from "./Footer";

function Home(){
  let data=useSelector(state => state.textArea);

  return(
    <div className="App">
      <div className="container">
      <Header />
      <Main />
      {
        data.map((d, i) => <Sub key={i+1} propsValue={d}/>)
      }
      <Footer />
      </div>
    </div>
  )
}

export default Home;