import TextAreaBtn from "./TextAreaBtn";
import { TextBox, Name, Inform, Desc } from "../styles/InterviewStyle";
function TextArea(p){
  let data=p.propsValue;
  let { name, job, interview}=data;

  return(
    <TextBox> 
      <Name>{name}</Name>
      <Inform>
        {
          job.map((d, i)=><p key={i+1}>{d}</p>)
        }
      </Inform>
      <Desc>
        {
          interview.map((d ,i)=><p key={i+1}>{d}</p>)
        }
      </Desc>
      <TextAreaBtn />
    </TextBox>
  )
};

export default TextArea;