import styled from 'styled-components';
import { font } from './FontStyle';

export const MainArea = styled.div`
	position: absolute;
	width: 100%;
	height: 100vh;
`
export const Title = styled.div`
  position: absolute;
  left: 50%;
  top: 50%;
  z-index: 11;
  font-size: 3.275em;
  transform: translate(-50%, -50%);
  color: #fff;
  mix-blend-mode: exclusion;
  text-align: center;
  font-weight: ${font.fontWeight["bold"]};
`
export const MainVideoBox = styled.div`
	position: relative;
	height: 100vh;
`
export const MainVideo = styled.video`
  width: 100%;
  height: 100%;
  object-fit: cover;
`