import { useSelector } from "react-redux";
import { DesktopNav, DesktopNavLi, DesktopNavLiA, MobileNav, MobileNavLi, MobileNavLiA } from "../styles/NavStyle";

function Nav(){
  let data=useSelector(state => state.navigation);

  return (
    <>
      {data.map((d, i) => {
        if (d.device === "mobile") {
          return (
            <MobileNav key={i + 1} id="mobile">
              <ul>
                {d.navigation.map((d, i) => (
                  <MobileNavLi key={i + 1}>
                    <MobileNavLiA href="">{d}</MobileNavLiA>
                  </MobileNavLi>
                ))}
              </ul>
            </MobileNav>
          );
        } else if (d.device === "gnb") {
          return (
            <DesktopNav key={i + 1} id="gnb" >
              <ul>
                {d.navigation.map((d, i) => (
                  <DesktopNavLi key={i + 1}>
                    <DesktopNavLiA href="">{d}</DesktopNavLiA>
                  </DesktopNavLi>
                ))}
              </ul>
            </DesktopNav>
          );
        } 
        else {
          return null; 
        }
      })}
    </>
  );
}

export default Nav;