import FooterColumnNav from "./FooterColumnNav";

import { FtColumnItem } from "../styles/FooterStyle";
function FooterColumn(p){
  let data=p.propsValue;
  let { title, ftNav }=data;

  return(
  <FtColumnItem>
    <strong>{title}</strong>
    <FooterColumnNav propsValue={ ftNav }/>
  </FtColumnItem>
)
};

export default FooterColumn;