import ContentWrap from "./ContentWrap";
import { SubBox, SubInner } from "../styles/SubStyle";

function Sub(p){
  let data=p.propsValue;
  return(

  <SubBox className="subpage">
    <SubInner>
      <ContentWrap propsValue={data}/>
    </SubInner>
  </SubBox>
  )
};

export default Sub;
