import styled from 'styled-components';

export const HeaderBox = styled.header`
  @media only screen and (max-width:720px){
    height: 50px;
    mix-blend-mode: normal;
    background-color: #fff;
    border-radius: 20px;
    width: 50%;
    left: 50%;
    transform: translateX(-50%);
    margin-top: 1.5em;
    overflow: hidden;
  }
  @media only screen and (max-width:499px){
    width: 300px;
  } 
  position: fixed;
  left: 0;
  top: 0;
  z-index: 10000;
  width: 100%;
  mix-blend-mode: difference;
`;
export const Inner = styled.div`
  @media only screen and (max-width:720px){
    height: 50px;
    padding: 0.5em 1em;
    align-items: center;
    box-sizing: border-box;
  }
  padding: 1em 1em 0 1em;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
`
export const Search = styled.div`
  @media only screen and (max-width:720px){
    display: block
  }
  display: none;
  width: 1.875em;
  height: 1.875em;
  background: url(./assets/images/search.svg) no-repeat center center;
  background-size: contain;
  text-indent: -9999px;
  overflow: hidden;
`
export const Logo = styled.h1`
  @media only screen and (max-width:720px){
    width: 4em;
    height: 2em;
    background: url(./assets/images/logo-black.svg) no-repeat center center;
    background-size: contain;
  }
  display: block;
  width: 7em;
  height: 4em;
  background: url(./assets/images/logo-white.svg) no-repeat 0 0 ;
  background-size: contain;
  text-indent: -9999px;
  overflow: hidden;
`

export const Tab = styled.div`
  @media only screen and (max-width: 720px){
    display: block;
  }
  display: none;
  cursor: pointer;
  height: 1.875em;
  width: 1.875em;
  overflow: visible;
  position: relative;
  z-index: 2;

  span,
  span:before,
  span:after {
    background: #000;
    display: block;
    height: 2px;
    opacity: 1;
    position: absolute;
    transition: 0.3s ease-in-out;
    width: 1.875em;
  }

  span:before,
  span:after {
    content: "";
  }

  span {
    right: 0px;
    top: 13px;
  }

  span:before {
    left: 0px;
    top: -10px;
  }

  span:after {
    left: 0px;
    top: 10px;
  }

  &.close span {
    transform: rotate(-45deg);
    top: 13px;
  }

  &.close span:before {
    top: 0px;
    transform: rotate(90deg);
  }

  &.close span:after {
    top: 0px;
    left: 0;
    transform: rotate(90deg);
    opacity: 0;
    width: 0;
  }
`;

