import styled from 'styled-components';

export const MobileNav = styled.nav`
  @media only screen and (max-width: 720px) {
    display: block;
  }
  display: none;
  position: absolute;
  left: 50%;
  top: 4em;
  transform: translateX(-50%);
`
export const MobileNavLi = styled.li`
  display: block;
  line-height: 2;
  text-align: center;
`
export const MobileNavLiA= styled.a`
  color: #000;
`
export const DesktopNav = styled.nav`
  @media only screen and (max-width:720px){ 
    display: none;
  }
`
export const DesktopNavLi = styled.li`

  text-align: right;
`
export const DesktopNavLiA = styled.a`
  display: block;
  color: #fff;
  line-height: 1.2;
`
