import { Swiper, SwiperSlide } from "swiper/react";

import { SwiperBox } from "../styles/SwiperStyle";
import 'swiper/css';
import 'swiper/css/navigation';

import SwiperAreaLi from "./SwiperAreaLi";

import { Navigation } from 'swiper/modules';

function SwiperArea(p){
  let data=p.propsValue;
  let { path }=data;
  return(
    <SwiperBox> 
    <Swiper
      modules={[Navigation]}
      navigation={true}
      slidesPerView={1}
      spaceBetween={10}
      breakpoints={{
        940 : {
          slidesPerView: 2,
          spaceBetween: 10,
        }
      }}
      className="content-swiper"
    >
      {
        path.map((d, i) => 
          <SwiperSlide key={i+1} ><SwiperAreaLi propsValue={d} /></SwiperSlide>
        )
      }
      
    </Swiper>
    </SwiperBox>
  )
};

export default SwiperArea;