import { useSelector } from "react-redux";

import FooterWrap from "./FooterWrap";

import { FooterBox, FtInner } from "../styles/FooterStyle";

function Footer(){
  let data=useSelector(state => state.footerColumn);

  return(
    <FooterBox className="footer"> 
      <FtInner>
        <FooterWrap propsValue={data}/>
      </FtInner>
    </FooterBox>
  )
};

export default Footer;