import styled from 'styled-components';
import { font } from './FontStyle';

export const FooterBox = styled.div`
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  background-color: #000;
  color: #fff;
  box-sizing: border-box;
  z-index: 100;
  background: linear-gradient(180deg, rgba(0,0,0,.1) 0%, rgba(0,0,0,1) 100%);
`;
export const FtInner = styled.div`
  @media only screen and (max-width:1580px) {
    width: 90%;
    margin: 0 auto;
  }
  @media only screen and (max-width:1280px) {
    top: auto;
  }
  @media only screen and (max-width:720px){
    top: auto;
    bottom: 3em;;
  }
  position: absolute;  
  top: auto;
  bottom: 3em;
  left: 50%;
  transform: translate(-50% , 0);
  margin: 0 auto;
  max-width: 1580px;
  width: 100%;
`;
export const FtContent = styled.div`
  @media only screen and (max-width:940px) {
    flex-direction: column;
    width: 80%;
    margin: 0 auto;
  }
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0 1em;
`;
export const FtColumnItem = styled.div`
@media only screen and (max-width:940px) {
		width: 100%;
    ul {
      display: none;
    }
    strong {
      padding: 1em 0;
      border-bottom: 1px solid #999;
      margin: 0;
    }
	}
  display: flex;
  flex-direction: column;
  width: calc((100% - 5px)/5);
  strong {
    margin-bottom: 1em;
    a {
      color: #fff;
    }
  }
  ul li {
    line-height: 1.5em;
    a {
      color: #fff;
      font-size: ${font.fontSize["s"]};
      font-weight: ${font.fontWeight["light"]};

      &:hover {
        text-decoration: underline;
      }
    }
  }
  &:last-child {
    display: flex;
    flex-direction: column;
    justify-content: center
  }
`;
export const FtAssetA = styled.a`
  display: block;
  color: #fff;
  text-align: center;
  font-weight: ${font.fontWeight["light"]};
  text-decoration: underline;
  vertical-align: center;
`;
export const FtAssetLogo = styled.a`
  margin: 0 auto;
  height: 4em;
  width: 8em;
  background: url(./assets/images/certilogo.png) no-repeat center center;
  background-size: contain;
  text-indent: -9999px;
  overflow: hidden;
`